import Header from '@/components/Header'
import ProductTrolley from '@/components/ProductTrolley'
import styles from './page.module.css'
import Link from 'next/link'
import { DB } from '@/lib/mysql'
import { redirect } from 'next/navigation'

export default async function home({ params }: any) {
  const id = params.id
  const producto: Producto = await DB.getProductById(id)
  // prettier-ignore

  async function procesarForm(data: FormData) {
    'use server';
   const producto : Partial<Producto> = {
    id_producto: id,
    nombre : data.get("nombre") as string,
    categoria : data.get("categoria") as string,
    marca : data.get("marca") as string,
   
    color : data.get("color") as string,
    url_imagen: data.get("url_img") as string,
   }
   
   
  }

  return (
    <main className={styles.principal}>
      <header className={styles.header}>
        <Link href={`/`}>
          <h1 className={styles.titulo}>PAPELAPP</h1>
        </Link>
      </header>
      <section className={styles.seccion}>
        <ProductTrolley />
      </section>
      <div className={styles.orden}>
        <button className={styles.bto}>Ordenar</button>
      </div>
    </main>
  )
}
